/* Accordian */
.accordion-button:not(.collapsed) {
  color: #5c2d90;
  background-color: rgb(91, 44, 143, 0.2);
  box-shadow: inset 0 -1px 0 rgb(0 0 0/13%);
}
.accordion-button:not(.collapsed)::after {
  background-image: url(data:image/svg+xml;base64,);
  transform: rotate(-180deg);
  color: #5c2d90;
}
::-moz-selection {
  color: #5c2d90;
  background: rgb(91, 44, 143, 0.2);
}
::selection {
  color: #5c2d90;
  background: rgb(91, 44, 143, 0.2);
}

/* Solid Line */
hr.solid {
  border-top: 3px solid #bbb;
}
/* Navbar */
.navStyle {
 
  height: calc(100% - 10px);
}
.navImg {
  width: 220px;
  padding: 20px;
}

/* header */
.masthead {
  background: linear-gradient(to left,rgb(0, 217, 255),rgb(243, 3, 115) );
  padding-bottom: 5rem;
}
.typewriter h2 {
  color: rgb(19, 17, 17);
  white-space: nowrap;
  font-weight: bolder;
  font-size: 6vh;
  overflow: hidden;
  border-right: 0.15em solid black;
  white-space: nowrap;
  margin: 0 auto;
  width: 20em;
  animation: typing 5.5s steps(40, end), blink-caret 1s step-end infinite;
  animation-iteration-count: infinite;
}
.typan {
  color: #fff;
  white-space: nowrap;
  font-weight: bolder;
  font-size: 3vh;
  overflow: hidden;
  width: 20em;
  animation: type 4s steps(60, end);
}
.typ:nth-child(2) {
  animation: type2 8s steps(60, end);
}
@keyframes type {
  from {
      width: 0;
  }
}
@keyframes type2 {
  0% {
      width: 0;
  }
  50% {
      width: 0;
  }
  100% {
      width: 100;
  }
}
@keyframes blink {
  to {
      opacity: 0;
  }
}

@keyframes typing {
  from {
      width: 0;
  }
  to {
      width: 100%;
  }
}
@keyframes blink-caret {
  from,
  to {
      border-color: transparent;
  }
  50% {
      border-color: black;
  }
}
